@font-face{
  font-family: VT323;
  src: url(VT323-Regular.ttf)
}
body{
  -ms-overflow-style: none; /* for Internet Explorer, Edge */
  scrollbar-width: none; /* for Firefox */
  overflow-x: hidden; 
}

#message{
  position: absolute;
  text-align: left;
}

#game{
  width: 1400px;
  height: 600px;
  border: 1px solid black;
  margin:0%;
}

#character{
  width: 256px;
  height: 400px;
  position: absolute;
  top: 300px; 
  scale:.5;
  left:125px; 
}
#connie{
  width: 256px;
  height: 380px;
  position: absolute;
  top: 285px; 
  scale:.60;
  left:75px; 
}
#penny{
  width: 256px;
  height: 380px;
  position: absolute;
  top: 305px; 
  scale:.5;
  left:0px; 
}


#hat{
  width: 256px;
  height: 400px;
  position: absolute;
  top:200px; 
  scale:.5;
  left:600px;
  background: url("./hat.png");

  
}
#label1{
  font-size: 24px;
  position: absolute;
  left: 160px;
  top: 600px;
  visibility:hidden;
}
#label2{
  font-size: 24px;
  position: absolute;
  left: 210px;
  top: 600px;
  visibility:hidden;
}
#label3{
  font-size: 24px;
  position: absolute;
  left: 275px;
  top: 600px;
  visibility:hidden;
}

#tree{
  width: 256px;
  height: 400px;
  position: absolute;
}#tree-container{
  width: 256px;
  height: 400px;
  position: absolute;
}

.tree{
  background: url("./tree.png");

}
.tree-star{
  position: absolute;
  background: url("./star.png");
  top:50px;
  left:50px;
  scale:.5;

}

@keyframes falling{
  /* 0%{transform: translate(100px, 50px);} */
  /* 100%{transform: translate(600px, 600px);} */
  0%{left:50px;top:50px;scale:.5;}
  100%{left:600px;top:500px;scale:1}
}

.animate-fall{
  /* background: url("./xmas-no-hat.png"); */
  animation: falling 1000ms linear;
}



@keyframes sprite-run {
  from { background-position: 0px; }
  to { background-position: 2560px; }
}
@keyframes cat-run {
  from { background-position: 0px; }
  to { background-position: 1024px; }
}

@keyframes tree-appear{
  0%{clip:rect(0px,256px,50px,0px)}
  25%{clip:rect(0px, 256px,100px,0px)}
  50%{clip:rect(0px, 256px,200px,0px)}
  75%{clip:rect(0px,256px,300px,0px)}
  100%{clip:rect(0px,256px,400px,0px)}

}

@keyframes tree-ascend{
  0%{top:500px;}
  25%{top:400px;}
  50%{top:300px;}
  75%{top:200px;}
  100%{top:200px;}
}
.ascend{
  animation: tree-ascend 4000ms linear;
}
@keyframes tree-blink{
  0%{ background: url("./tree.png");}
  100%{ background: url("./tree-glow.png");}
}
.blink{
  animation: tree-blink 3000ms infinite;

}


.appear{
  
  animation: tree-appear 4000ms linear;
 
}


.run{
  animation: sprite-run 800ms linear;
  animation-timing-function: steps(10);
  animation-iteration-count: infinite;
}

.run-cat{
  animation: cat-run 400ms linear;
  animation-timing-function: steps(4);
  animation-iteration-count: infinite;
}

.flip{
  transform: scaleX(-1);
}

@keyframes blink{
  0%{opacity: 0%;}
  100%{opacity: 100%;}
}

.animate-blink{
  animation: blink 1000ms infinite;
}


@keyframes jump{
  0%{top: 150px;}
  30%{top: 100px;}
  70%{top: 100px;}
  100%{top: 150px;}
}

.animate-jump{
  background: url("./xmas-no-hat.png");
  animation: jump 300ms linear;
}

.background-run{
  background: url("./xmas-no-hat.png");
}

.background-stand{
